import React from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import styled from 'styled-components';

const CodesandboxTemplate = ({ data }) => {
  const { html, frontmatter } = data.markdownRemark;

  const isBrowser = typeof window !== "undefined"

  let url = "https://codesandbox.io/embed";
  let code = "";
  if(isBrowser){
    const params = new Proxy(new URLSearchParams(window.location.search), {
      get: (searchParams, prop) => searchParams.get(prop),
    });

    if (params.code) {
      code = "quirky-ellis-8olfq4";
      url = url + "/" + params.code;
    } else {
      url = url + "/quirky-ellis-8olfq4";
    }

  } else {
    url = url + "/quirky-ellis-8olfq4";
  }
  

  url = url + "?fontsize=14&theme=dark";


  return (
    <Layout title={frontmatter.title}>
      <PostWrapper>
        <PostContent dangerouslySetInnerHTML={{ __html: html }} />
        <iframe src={url}
          style={{ width: "100%", height: "500px", border: 0, borderRadius: "4px", overflow: "hidden" }}
          title={code ? code : "quirky-ellis-8olfq4"}
          allow="accelerometer; ambient-light-sensor; camera; encrypted-media; geolocation; gyroscope; hid; microphone; midi; payment; usb; vr; xr-spatial-tracking"
          sandbox="allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts"
        ></iframe>
      </PostWrapper>
    </Layout>
  );
};

export default CodesandboxTemplate;

const PostWrapper = styled.div`
  padding-top: var(--size-900);
  padding-bottom: var(--size-900);
  margin-left: auto;
  margin-right: auto;
  max-width: 70ch;
  word-wrap: break-word;
`;

const PostContent = styled.section`
  padding-top: var(--size-800);

  & > * + * {
    margin-top: var(--size-300);
  }

  & > p + p {
    margin-top: var(--size-700);
  }

  * + h1,
  * + h2,
  * + h3 {
    margin-top: var(--size-900);
  }

  h1 {
    font-size: var(--size-800);
  }

  h2 {
    font-size: var(--size-700);
  }

  h3 {
    font-size: var(--size-700);
  }

  b,
  strong {
    font-weight: 600;
  }

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-thickness: 0.125rem;
  }

  blockquote {
    padding-left: var(--size-400);
    border-left: 5px solid;
    font-style: italic;
  }

  code {
    font-family: 'Source Sans Pro', monospace;
    overflow-x: auto;
    white-space: pre-wrap;
  }

  pre {
    overflow-x: auto;
    white-space: pre-wrap;
    max-width: 100%;
  }
`;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        profile_image
      }
    }
  }
`;
